.cl-banner-root {
    position: relative;
    background-color: #fcf8ef !important;
    height: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    overflow: hidden;
  }
  
  .cl-banner-title {
    font-weight: 600 !important;
    color: #1A1729 !important;
    font-family: 'Urbanist', sans-serif !important;
    font-size: 70px !important;
    margin-top: 20px !important;
  }
  
  .cl-decor-icon {
    position: absolute;
    opacity: 0.6;
  }
  
  .cl-circle {
    bottom: 10%;
    left: 5%;
    color: gold;
    font-size: 40px;
  }
  
  .cl-zigzag {
    top: 10%;
    left: 15%;
    color: #00aa99;
    font-size: 40px;
  }
  
  .cl-blur {
    top: 5%;
    right: 5%;
    color: #fbc02d;
    font-size: 40px;
  }
  
  .cl-triangle {
    bottom: 10%;
    right: 10%;
    color: purple;
    font-size: 40px;
    transform: rotate(45deg);
  }
  
  .cl-dots {
    top: 45%;
    left: 2%;
    color: #ffca28;
    font-size: 35px;
  }
  
  .cl-course-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 80px 40px;
    background-color: #fff !important;
  }
  
  .cl-course-list-subtitle {
    font-family: 'Urbanist', sans-serif !important;
    font-weight: 700 !important;
    color: #F2BC00 !important;
    font-size: 20px !important;
    margin-top: 10px !important;
    text-align: center;
  }
  
  .cl-course-list-title {
    font-family: 'Urbanist', sans-serif !important;
    font-weight: 700 !important;
    color: #1A1729 !important;
    font-size: 55px !important;
    margin-top: 10px !important;
    text-align: center;
  }
  
  .cl-course-list-cards {
    width: 100%;
    max-width: 1200px;
    margin-top: 60px;
  }
  
  .cl-pagination {
    margin-top: 40px;
  }
  

.cl-course-list-cards{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    margin-top: 60px;
}

